.curr_row {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0.8rem;
}
.curr_row > input {
  border: 3px linear-gradient(90deg, #fc466b 0%, #3f5efb 100%);
  padding: 0.25rem;
  width: 15em;
  border-radius: 0.3em;
}
.curr_row > select {
  padding: 0.1rem;
  margin-left: 0.5rem;
}
