* {
  margin: 0;
}
.app {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: black;
  min-height: 100vh;
  color: white;
  text-align: center;
}
.wrapper {
  border: 2px solid;
  border-image-slice: 1;
  padding: 50px;
  border-image-source: linear-gradient(to left, #743ad5, #d53a9d);
}
h3 {
  margin: 0;
  margin-bottom: 0.5rem;
}
